<template>
    <f-form-modal
        :size="size"
        :data-test="testId"
        :use-error-list="false"
        :is-open="isOpen"
        :value="value"
        :before-submit="onBeforeSubmit"
        @submit="onSubmit"
        @cancel="onCancel"
        @close="onClose"
    >
        <template #header>Trutensilierna</template>
        Trutensilierna i ett tryckeri ro ingalunda en oviktig faktor, fr trevnadens, ordningens och ekonomiens
        upprtthllande, och dock r det icke sllan som sorgliga erfarenheter gras p grund af det ofrstnd med
        hvilket kaster, formbrden och regaler tillverkas och frsljas Kaster som ro dligt hopkomna och af
        otillrckligt.
        <template #error-message>Oj, du har glmt fylla i ngot. G till:</template>
        <template #input-text-fields>
            <f-text-field
                v-model="value.field1"
                v-validation.required.maxLength="{ maxLength: { length: 32 } }"
                class="field1"
            >
                Frnamn
            </f-text-field>
            <f-text-field
                v-model="value.field2"
                v-validation.required.maxLength="{ maxLength: { length: 32 } }"
                class="field2"
            >
                Efternamn
            </f-text-field>
        </template>
    </f-form-modal>
</template>

<script>
import { FFormModal, FTextField } from "@fkui/vue";

export default {
    name: "ExampleModal",
    components: { FFormModal, FTextField },
    props: {
        isOpen: {
            type: Boolean,
            default: false,
            required: false,
        },
        size: {
            default: "",
            type: String,
        },
        testId: {
            type: String,
            default: "",
            required: false,
        },
    },
    data: () => {
        return {
            value: {
                field1: "",
                field2: "",
            },
        };
    },
    methods: {
        onSubmit(event) {
            this.$emit("submit", event);
        },
        onCancel(event) {
            this.$emit("cancel", event);
        },
        onClose(event) {
            this.$emit("close", event);
        },
        onBeforeSubmit() {
            // eslint-disable-next-line no-console
            console.log(Date.now(), "onBeforeSubmit consumer");
        },
    },
};
</script>
